header {
  width: 1440px;
  height: 28px;
  margin: 0 auto;
  font-size: 12px;
  line-height: 28px;
  font-weight: 400px;
  background-color: #545652;
  overflow: hidden;
}
header a {
  color: #d5d5d5;
}
header .header-left {
  float: left;
}
header .header-left li {
  float: left;
  text-align: center;
  padding: 0 12px;
}
header .header-right {
  float: right;
  margin-right: 12px;
}
header .header-right li {
  float: left;
  text-align: center;
  padding: 0 8px;
}
header .header-right li:first-child {
  margin-right: 5px;
}
.search {
  padding: 10px 0 0;
  background-color: #f0f3f5;
  position: relative;
  margin-bottom: 40px;
}
.search .top {
  overflow: hidden;
  border-bottom: 1px solid #e3ebec;
}
.search .top .logo {
  float: left;
  margin-right: 13px;
  background: url('./assets/logo.png') no-repeat left 12px;
}
.search .top .logo a {
  display: block;
  width: 145px;
  height: 56px;
}
.search .top .input {
  float: left;
  padding: 10px 0 15px 0;
  color: #111111;
}
.search .top .input .ant-input-wrapper.ant-input-group {
  width: 507px;
  height: 30px;
}
.search .top .input .ant-input {
  padding: 0;
  padding-left: 10px;
  font-size: 13px;
  height: 32px;
}
.search .top .input .ant-input-group-addon {
  height: 30px;
}
.search .top .input .ant-input-search-button {
  padding: 0;
  background-color: none;
  width: 34px;
  height: 32px;
  border: none;
  background: url('./assets/search-btn-bg.png') no-repeat 0 -40px;
}
.search .top .input .ant-input-search-button i {
  display: none;
}
.search .bottom ul {
  overflow: hidden;
  margin: 9px 0 9px 0;
}
.search .bottom ul li {
  float: left;
  font-size: 14px;
  margin-right: 25px;
}
.search .bottom ul li a {
  color: #27a;
}
.search .rank {
  display: block;
  width: 186px;
  height: 96px;
  position: absolute;
  top: 10px;
  left: 50%;
  margin-left: 200px;
  background: url('./assets/rankList.png') no-repeat center;
  background-size: 100%;
}
